"use client"

import { useState, useContext } from "react"
import ImgHumar1 from '../assets/images/miners/leaf.png'
import ImgHumar2 from '../assets/images/miners/steel.png'
import ImgHumar3 from '../assets/images/miners/diamond.png'
import '../index.css'
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract, useDisconnect } from "wagmi";
import { useConnectModal } from "@rainbow-me/rainbowkit";
import { zeroAddress } from "viem";
import toast from "react-hot-toast";
import { ethers } from "ethers";

import {
  MINER_ADDRESS_MAIN,
  MINER_ADDRESS_BASE,
  HASH_ADDRESS_BASE,
  HASH_ADDRESS_MAIN,
  BASE_NET,
  UNIT256_MAX
} from "../config"
import HashAbi from "../config/abis/HASH_ABI.json";
import MinerAbi from "../config/abis/MINER_ABI.json";

import ImgLeaf from "../assets/images/miners/leaf.png"
import ImgSteel from "../assets/images/miners/steel.png"
import ImgDiamond from "../assets/images/miners/diamond.png"
import { RefreshContext } from '../context/RefreshContext'

const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, slowRefresh: slow }
}

export default function Space() {
  const { fastRefresh } = useRefresh();

  const [activeItem, setActiveItem] = useState()
  const [selectedItem, setSelectedItem] = useState(-1)

  const [price, setPrice] = useState(0);

  const [isPurchasingMiner, setIsPurchasingMiner] = useState([]);
  const [isUpgradingMiner, setIsUpgradingMiner] = useState([]);
  const [isListingMiner, setIsListingMiner] = useState(false);

  const [showModal, setShowModal] = useState(false);
  const [showSellModal, setShowSellModal] = useState(false);

  const { data: txResult, writeContractAsync } = useWriteContract();

  const { openConnectModal } = useConnectModal();
  const { disconnect } = useDisconnect();

  const chainId = useChainId();
  const { isConnected, address } = useAccount();
  const { data: balance, refetch: refetchBalance } = useBalance({ address: address });

  ////////HASH//////////////////////////////////////
  const { data: tokenBalance, refetch: refetchTokenBalance } = useReadContract({
    address: chainId === BASE_NET ? HASH_ADDRESS_BASE : HASH_ADDRESS_MAIN,
    abi: HashAbi,
    functionName: "balanceOf",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: approvedTokenAmount, refetch: refetchApprovedTokenAmount } = useReadContract({
    address: chainId === BASE_NET ? HASH_ADDRESS_BASE : HASH_ADDRESS_MAIN,
    abi: HashAbi,
    functionName: "allowance",
    args: [isConnected ? address : zeroAddress, chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN],
    chainId: chainId,
  });

  /////// Miner //////////////////////////////////
  const { data: playerMinersPaginated, refetch: refetchPlayerMinersPaginated } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "getPlayerMinersPaginated",
    args: [isConnected ? address : zeroAddress, 0, isConnected ? 100 : 0],
    chainId: chainId,
  });

  const { data: ownerToFacility, refetch: refetchOwnerToFacility } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "ownerToFacility",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: leafCost, refetch: refetchLeafCost } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "getTokenAmountFromEthAmount",
    args: ['2500000000'],
    chainId: chainId,
  });
  const { data: steelCost, refetch: refetchSteelCost } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "getTokenAmountFromEthAmount",
    args: ['25000000000'],
    chainId: chainId,
  });
  const { data: diamondCost, refetch: refetchDiamondCost } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "getTokenAmountFromEthAmount",
    args: ['250000000000'],
    chainId: chainId,
  });
  const { data: isTokenTradeable, refetch: refetchIsTokenTradeable } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "isTokenTradeable",
    args: [],
    chainId: chainId,
  });

  useState(() => {
    refetchTokenBalance();
    refetchApprovedTokenAmount();
    refetchIsTokenTradeable();
  }, [fastRefresh])

  const availableMiners = !(playerMinersPaginated && playerMinersPaginated?.length > 0) ? [] : playerMinersPaginated.filter(item => Number(item.x) === 100 && !item.isListedSell);

  const handleBuyMiner = async (minerIndex) => {
    if (!isConnected || !address) {
      toast.error("Please connect your wallet first.");
      return;
    }
    if (!isTokenTradeable) {
      toast.error("$HASH is not available yet!");
      return;
    }
    if (!balance || parseFloat(ethers.formatEther(balance.value)) < 0.0001) {
      toast.error("Insufficient gas fee.");
      return;
    }
    const tokenAmount = minerIndex === 1 ? ethers.formatEther(leafCost) : minerIndex === 2 ? ethers.formatEther(steelCost) : ethers.formatEther(diamondCost);
    if (!tokenAmount || parseFloat(ethers.formatEther(tokenBalance)) < parseFloat(tokenAmount)) {
      toast.error("Insufficient token amount.");
      return;
    }

    let temp = isPurchasingMiner;
    temp[minerIndex] = true;
    setIsPurchasingMiner(temp);
    if (!tokenAmount || parseFloat(ethers.formatEther(approvedTokenAmount)) < parseFloat(tokenAmount)) {
      await writeContractAsync({
        address: chainId === BASE_NET ? HASH_ADDRESS_BASE : HASH_ADDRESS_MAIN,
        abi: HashAbi,
        functionName: "approve",
        args: [chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN, UNIT256_MAX],
        value: '0'
      })
        .then(() => {
          temp[minerIndex] = false;
          setIsPurchasingMiner(temp);
        })
        .catch((err) => {
          temp[minerIndex] = false;
          setIsPurchasingMiner(temp);
        });
    }
    await writeContractAsync({
      address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
      abi: MinerAbi,
      functionName: "buyMiner",
      args: [minerIndex],
      value: '0'
    })
      .then(() => {
        setTimeout(async () => {
          temp[minerIndex] = false;
          setIsPurchasingMiner(temp);
          setShowModal(false);
          await refetchPlayerMinersPaginated();
          await refetchOwnerToFacility();
        }, 3000); // Keep animation running for a bit after Claim completes
      })
      .catch((err) => {
        temp[minerIndex] = false;
        setIsPurchasingMiner(temp);
      });
  }

  const handleSellMiner = async () => {
    setIsListingMiner(true);
    if (!isConnected || !address) {
      toast.error("Please connect your wallet first.");
      setIsListingMiner(false);
      return;
    }
    if (!balance || parseFloat(ethers.formatEther(balance.value)) < 0.0001) {
      toast.error("Insufficient gas fee.");
      setIsListingMiner(false);
      return;
    }
    if (price <= 0) {
      toast.error("Please input valid price.");
      setIsListingMiner(false);
      return;
    }
    await writeContractAsync({
      address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
      abi: MinerAbi,
      functionName: "listForSale",
      args: [activeItem?.id, ethers.parseEther(price)],
      value: '0'
    })
      .then(() => {
        setTimeout(async () => {
          setIsListingMiner(false);
          setShowSellModal(false);
          await refetchPlayerMinersPaginated();
          await refetchOwnerToFacility();
        }, 3000); // Keep animation running for a bit after Claim completes
      })
      .catch((err) => {
        setIsListingMiner(false);
        console.log(err);
      });
  }

  const handleUpgradeLevel = async (minerId) => {
    if (!isConnected || !address) {
      toast.error("Please connect your wallet first.");
      return;
    }
    if (!isTokenTradeable) {
      toast.error("$HASH is not available yet!");
      return;
    }
    const miner = availableMiners.find(i => i.id.toString() === minerId.toString());
    if (miner.level.toString() === "5") {
      toast.error("Miner is already at max level.");
      return;
    }
    if (!balance || parseFloat(ethers.formatEther(balance.value)) < 0.0001) {
      toast.error("Insufficient gas fee.");
      return;
    }
    const minerIndex = Number(miner.minerIndex).toString();
    const tokenAmount = minerIndex === "3" ? ethers.formatEther(diamondCost) : minerIndex === "2" ? ethers.formatEther(steelCost) : ethers.formatEther(leafCost);
    if (!tokenAmount || parseFloat(ethers.formatEther(tokenBalance)) < parseFloat(tokenAmount)) {
      toast.error("Insufficient token amount.");
      return;
    }

    let temp = isUpgradingMiner;
    temp[minerId] = true;
    setIsUpgradingMiner(temp);

    if (!tokenAmount || parseFloat(ethers.formatEther(approvedTokenAmount)) < parseFloat(tokenAmount)) {
      console.log("Approving token for upgrade...");
      await writeContractAsync({
        address: chainId === BASE_NET ? HASH_ADDRESS_BASE : HASH_ADDRESS_MAIN,
        abi: HashAbi,
        functionName: "approve",
        args: [chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN, UNIT256_MAX],
        value: '0'
      })
        .then(() => {
          temp[minerId] = false;
          setIsUpgradingMiner(temp);
        })
        .catch((err) => {
          temp[minerId] = false;
          setIsUpgradingMiner(temp);
        });
    }
    await writeContractAsync({
      address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
      abi: MinerAbi,
      functionName: "updateMiner",
      args: [minerId]
    })
      .then(() => {
        setTimeout(async () => {
          temp[minerId] = false;
          setIsUpgradingMiner(temp);
          setShowModal(false);
          await refetchPlayerMinersPaginated();
          await refetchOwnerToFacility();
        }, 3000); // Keep animation running for a bit after Claim completes
      })
      .catch((err) => {
        temp[minerId] = false;
        setIsUpgradingMiner(temp);
      });
  }

  const getMinerTypeColor = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return "text-green-400"
      case 2:
        return "text-gray-400"
      case 3:
        return "text-blue-400"
      default:
        return "text-gray-400"
    }
  }

  const getMinerTypeName = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return "Leaf"
      case 2:
        return "Steel"
      case 3:
        return "Diamond"
      default:
        return "Unknown"
    }
  }

  const getMinerImage = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return ImgLeaf
      case 2:
        return ImgSteel
      case 3:
        return ImgDiamond
      default:
        return ImgLeaf
    }
  }

  return (
    <div className="col-span-2 rounded-b-lg space-y-6 p-4 bg-[#1B2531]">
      <div className="flex justify-between">
        <div className="space-y-2 w-1/2 text-xl">
          <div className="text-yellow-400 font-medium">ID: #{activeItem?.id.toString() ?? "-"}</div>
          <div className="text-yellow-400 font-medium">Level: {activeItem?.level.toString() ?? "-"}</div>
          <div className="text-yellow-400 font-medium" onClick={() => console.log(activeItem)}>Power: {activeItem ? (activeItem?.hashrate * activeItem?.level).toString() : "-"} GH</div>
        </div>
        {activeItem?.id.toString() ? <div className="bg-black w-1/2 rounded-lg border border-slate-600 p-2 aspect-square max-w-28 max-h-28">
          <div className="relative w-full h-full">
            <div className="absolute top-1 left-1 text-xs text-yellow-400 font-bold">#{activeItem?.id.toString() ?? ""}</div>
            <div className="absolute top-1 right-1 text-xs text-white bg-slate-700/80 px-1.5 py-0.5 rounded">Lv {activeItem?.level.toString() ?? ""}</div>
            <div className="flex items-center justify-center h-full">
              <div className="relative w-full h-full flex items-center justify-center">
                <img src={activeItem?.minerIndex.toString() === "1" ? ImgHumar1 : activeItem?.minerIndex.toString() === "2" ? ImgHumar2 : ImgHumar3} alt="miner" />
              </div>
            </div>
          </div>
        </div> : <></>}
      </div>

      {/* <div className="border-t border-slate-700/50"></div> */}

      {/* 
      <div className="space-y-3">
        <h3 className="text-white font-medium text-xl">Move Miner</h3>
        <div className="flex items-center space-x-4">
          <span className="text-white text-xl mr-3">A</span>
          <div className="flex items-center justify-center">
            <img src={LeftArrow} alt="" className="w-[25px]" />
            <img src={RightArrow} alt="" className="w-[25px]" />
          </div>
          <span className="text-white text-xl ml-3">B</span>
          <button className="px-3 py-1 text-xl text-white font-medium">SWAP</button>
        </div>
      </div> */}

      <div className="border-t border-slate-700/50"></div>

      {/* <div className="space-y-3">
        <h3 className="text-yellow-400 font-medium text-xl">Sell Miner</h3>
        <div className="flex space-x-2">
          <div className="relative flex-1">
            <input
              type="text"
              placeholder="Price"
              className="w-full px-3 text-xl py-2 bg-[#15120D] border-2 border-yellow-500 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:border-yellow-400 pr-12"
            />
            <span className="absolute right-3 text-xl top-1/2 transform -translate-y-1/2 text-yellow-400 font-medium">ETH</span>
          </div>
          <button className="px-4 py-2 text-yellow-500 border-2 border-yellow-500 rounded-lg text-sm font-medium">LIST</button>
        </div>
      </div> */}

      <div className="grid lg:grid-cols-3  grid-cols-2 lg:gap-4 gap-1 mt-6 lg:h-[400px] overflow-y-auto lg:p-3 scrollbar-thin scrollbar-thumb-[#F8A82C]/30 scrollbar-track-transparent">
        {[null, ...availableMiners].map((item, index) => (
          <div
            key={index}
            onClick={(event) => {
              setSelectedItem(index)
              if (!item) {
                setShowModal(true)
              } else {
                setActiveItem(availableMiners[index - 1])
              }
            }}
            className={`group relative aspect-square flex items-center justify-center bg-[#0000009e] backdrop-blur-sm rounded-xl cursor-pointer transition-all duration-300 hover:scale-105 ${index === selectedItem
              ? "border-[#F8A82C] shadow-lg shadow-[#F8A82C]/30 border-4"
              : "border-gray-600/50 border-2 hover:border-[#F8A82C]/50"
              }`}
          >
            {item ? (
              <>
                {/* Gradient overlay for miner type */}
                <div
                  className={`absolute inset-0 bg-gradient-to-br ${getMinerTypeColor(
                    item.minerIndex,
                  )} rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300`}
                ></div>

                {/* Miner ID */}
                <div className="absolute top-2 left-2 bg-[#F8A82C]/90 text-black lg:text-xs text-[10px] font-bold px-2 py-1 rounded-full z-10">
                  #{item?.id.toString()}
                </div>

                {/* Level Badge */}
                <div className="absolute top-2 right-2 bg-gradient-to-r from-[#F9A82C] to-[#FFD100] text-black lg:text-xs text-[10px] font-bold px-2 py-1 rounded-full z-10">
                  Lv {item?.level.toString()}
                </div>

                {/* Miner Type Badge */}
                <div className="absolute top-10 right-2 bg-black/80 text-[#F8A82C] text-xs font-semibold px-2 py-1 rounded-full z-10">
                  {getMinerTypeName(item.minerIndex)}
                </div>

                {/* Miner Image */}
                <div className="relative p-4 h-full w-full flex items-center justify-center">
                  <div className="relative w-full h-full flex items-center justify-center">
                    <img
                      src={getMinerImage(item.minerIndex) || "/placeholder.svg"}
                      alt={`${getMinerTypeName(item.minerIndex)} miner`}
                      className="max-w-full max-h-full object-contain filter group-hover:brightness-110 transition-all duration-300"
                    />
                  </div>
                </div>

                {/* List Miner Button */}
                <button
                  className="absolute lg:bottom-10 bottom-[31px] left-1/2 transform -translate-x-1/2 bg-gradient-to-r from-[#F9A82C] to-[#FFD100] hover:from-[#FFD100] hover:to-[#F9A82C] disabled:from-gray-600 disabled:to-gray-700 text-black disabled:text-gray-400 lg:text-xs text-[12px] font-bold lg:px-3 lg:py-2 py-[2px] mb-1 rounded-lg transition-all duration-300 hover:scale-105 active:scale-95 disabled:hover:scale-100 disabled:cursor-not-allowed w-28 z-10"
                  onClick={(event) => {
                    event.stopPropagation()
                    setSelectedItem(index)
                    setActiveItem(availableMiners[index - 1])
                    setShowSellModal(true)
                  }}
                  disabled={isListingMiner}
                >
                  {isListingMiner ? (
                    <div className="flex items-center justify-center space-x-1">
                      <svg className="animate-spin h-3 w-3" fill="none" viewBox="0 0 24 24">
                        <circle
                          className="opacity-25"
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                        ></circle>
                        <path
                          className="opacity-75"
                          fill="currentColor"
                          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        ></path>
                      </svg>
                      <span>Listing...</span>
                    </div>
                  ) : (
                    "Sell Miner"
                  )}
                </button>

                {/* Update Level Button */}
                <button
                  className="absolute bottom-2 left-1/2 transform -translate-x-1/2 bg-gray-700/90 hover:bg-gray-600/90 disabled:bg-gray-800/90 text-white disabled:text-gray-500 lg:text-xs text-[12px] font-semibold lg:px-3 lg:py-2 py-[2px] rounded-lg transition-all duration-300 hover:scale-105 active:scale-95 disabled:hover:scale-100 disabled:cursor-not-allowed w-28 z-10"
                  onClick={(event) => {
                    event.stopPropagation()
                    setActiveItem(availableMiners[index - 1])
                    setSelectedItem(index)
                    handleUpgradeLevel(item?.id.toString())
                  }}
                  disabled={isUpgradingMiner[item?.id]}
                >
                  {isUpgradingMiner[item?.id] ? (
                    <div className="flex items-center justify-center space-x-1">
                      <svg className="animate-spin h-3 w-3" fill="none" viewBox="0 0 24 24">
                        <circle
                          className="opacity-25"
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                        ></circle>
                        <path
                          className="opacity-75"
                          fill="currentColor"
                          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        ></path>
                      </svg>
                      <span>Updating...</span>
                    </div>
                  ) : (
                    "Update Level"
                  )}
                </button>
              </>
            ) : (
              // Buy Miner Card
              <div className="flex flex-col items-center justify-center space-y-3 p-4">
                <div className="text-4xl mb-2"></div>
                <h2 className="text-[#F8A82C] font-bold text-lg lg:text-xl text-center leading-tight">Buy Miner</h2>
                <div className="w-8 h-1 bg-gradient-to-r from-[#F9A82C] to-[#FFD100] rounded-full"></div>
                <p className="text-gray-400 text-xs text-center">Click to purchase</p>
              </div>
            )}

            {/* Selection Glow Effect */}
            {index === selectedItem && (
              <div className="absolute inset-0 bg-gradient-to-r from-[#F8A82C]/10 to-[#FFD100]/10 rounded-xl pointer-events-none"></div>
            )}
          </div>
        ))}
      </div>
      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-[#1E1E1E] p-6 rounded-lg w-[90%] max-w-md text-center relative border-2 border-solid border-[#F9A82C]">
            <h2 className="text-[25px] font-bold mb-2 text-white">BUY MINER</h2>
            <p className="text-sm text-gray-300 mb-2">Buy more miners!</p>
            <img src="/Vector.svg" alt="Igloo" className="mx-auto my-4 w-50 h-auto" />
            <p className="text-left text-[#F9A82C] font-medium mb-2">Miners</p>
            <div className="bg-[#2B2B2B] p-4 rounded-md text-left text-sm">
              <div className="flex justify-between mb-1 font-medium">
                <span>- Available Spaces</span>
                <span className="text-yellow-400 font-medium">{`${ownerToFacility ? Number(ownerToFacility[1]) - Number(ownerToFacility[2]) : `-`}`}</span>
              </div>
              <div className="flex justify-between mb-1 font-medium">
                <span>- Leaf Miner</span>
                <span className="text-yellow-400 font-medium">{`${leafCost ? parseFloat(ethers.formatEther(leafCost)).toFixed(2) : `-`} $Hash`}</span>
              </div>
              {Number(ownerToFacility[1]) > 3 ? <div className="flex justify-between mb-1 font-medium">
                <span>- Steel Miner</span>
                <span className="text-yellow-400 font-medium">{`${steelCost ? parseFloat(ethers.formatEther(steelCost)).toFixed(2) : `-`} $Hash`}</span>
              </div> : <></>}
              {Number(ownerToFacility[1]) > 5 ? <div className="flex justify-between mb-1 font-medium">
                <span>- Diamond Miner</span>
                <span className="text-yellow-400 font-medium">{`${diamondCost ? parseFloat(ethers.formatEther(diamondCost)).toFixed(2) : `-`} $Hash`}</span>
              </div> : <></>}
            </div>
            <div className="grid grid-cols-2 gap-3 mt-6">
              <button
                onClick={() => setShowModal(false)}
                className="w-full bg-gray-700 hover:bg-gray-600 text-white py-2 rounded-md"
              >
                Cancel
              </button>
              <button
                onClick={() => handleBuyMiner(1)}
                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
              >
                {isPurchasingMiner[1] ? 'Purchasing...' : 'Buy Leaf Miner'}
              </button>
              {Number(ownerToFacility[1]) > 3 ? <button
                onClick={() => handleBuyMiner(2)}
                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
              >
                {isPurchasingMiner[2] ? 'Purchasing...' : 'Buy Steel Miner'}
              </button> : <></>}
              {Number(ownerToFacility[1]) > 5 ? <button
                onClick={() => handleBuyMiner(3)}
                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
              >
                {isPurchasingMiner[3] ? 'Purchasing...' : 'Buy Diamond Miner'}
              </button> : <></>}
            </div>
          </div>
        </div>
      )}

      {/* Modal */}
      {showSellModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-[#1E1E1E] p-6 rounded-lg w-[90%] max-w-md text-center relative border-2 border-solid border-[#F9A82C]">
            <h2 className="text-[25px] font-bold mb-2 text-white">Sell Miner</h2>
            <p className="text-sm text-gray-300 mb-2">List your miner on marketplace!</p>
            <img src="/Vector.svg" alt="Igloo" className="mx-auto my-4 w-50 h-auto" />
            <p className="text-left text-[#F9A82C] font-medium mb-2">Miners</p>
            <div className="bg-[#2B2B2B] p-4 rounded-md text-left text-sm">
              <div className="relative flex-1">
                <input
                  type="text"
                  placeholder="Price"
                  className="w-full px-3 text-xl py-2 bg-[#15120D] border-2 border-yellow-500 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:border-yellow-400 pr-12"
                  onChange={(e) => {
                    const value = e.target.value;
                    if (!isNaN(value) && value >= 0) {
                      setPrice(value);
                    } else {
                      toast.error("Please enter a valid price.");
                    }
                  }}
                />
                <span className="absolute right-3 text-xl top-1/2 transform -translate-y-1/2 text-yellow-400 font-medium">ETH</span>
              </div>
            </div>
            <div className="grid grid-cols-2 gap-3 mt-6">
              <button
                onClick={() => setShowSellModal(false)}
                className="w-full bg-gray-700 hover:bg-gray-600 text-white py-2 rounded-md"
              >
                Cancel
              </button>
              <button
                onClick={() => handleSellMiner()}
                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
              >
                {isListingMiner ? 'Listing...' : 'List Miner'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
