import { useEffect, useContext, useState } from "react";
import './RightPanel.scss'

import { useMediaQuery } from '@uidotdev/usehooks'
import { useChainId, useAccount, useReadContract } from "wagmi";
import { zeroAddress } from "viem";

import 'react-tabs/style/react-tabs.scss'
import {
    BASE_NET,
    MINER_ADDRESS_BASE,
    MINER_ADDRESS_MAIN,
} from "../config";
import MinerAbi from "../config/abis/MINER_ABI.json";
import { RefreshContext } from '../context/RefreshContext'

import SpacePanel from "./SpacePanel";
import SpacePanelFree from "./SpacePanelFree";
import BuySpace from "./BuySpace";

// Add animation keyframes style
const nftAnimationStyle = document.createElement('style');
nftAnimationStyle.innerHTML = `
  @keyframes pulse {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.05); opacity: 0.9; }
  }
  
  @keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
    100% { transform: translateY(0px); }
  }
  
  @keyframes glow {
    0% { opacity: 0.5; }
    100% { opacity: 0.8; }
  }
`;
document.head.appendChild(nftAnimationStyle);

const useRefresh = () => {
    const { fast, slow } = useContext(RefreshContext)
    return { fastRefresh: fast, slowRefresh: slow }
}

export default function RightPanel() {
    const isMobile = useMediaQuery("only screen and (max-width: 768px)");
    const { fastRefresh } = useRefresh();

    const chainId = useChainId();
    const { isConnected, address } = useAccount();

    /////// Miner //////////////////////////////////
    const { data: initializedStarterFacility, refetch: refetchInitializedStarterFacility } = useReadContract({
        address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "initializedStarterFacility",
        args: [isConnected ? address : zeroAddress],
        chainId: chainId,
    });

    const { data: ownerToFacility, refetch: refetchOwnerToFacility } = useReadContract({
        address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "ownerToFacility",
        args: [isConnected ? address : zeroAddress],
        chainId: chainId,
    });

    const [isUsedFreeSpace, setIsUsedFreeSpace] = useState(false);

    useEffect(() => {
        refetchInitializedStarterFacility();
        refetchOwnerToFacility();
    }, [fastRefresh]);

    useEffect(() => {
        if (ownerToFacility === undefined) return;

        if (Number(ownerToFacility[0]) === 0 && Number(ownerToFacility[1]) === 1 && Number(ownerToFacility[2]) === 1)
            setIsUsedFreeSpace(true);
        else
            setIsUsedFreeSpace(false);
    }, [ownerToFacility])

    return (
        <>
            {
                !initializedStarterFacility ?
                    (
                        // isUsedFreeSpace ?
                        //     <div className="">
                        //         <SpacePanelFree />
                        //     </div>
                        //     :
                        <BuySpace />
                    )
                    :
                    <div className="">
                        <SpacePanel />
                    </div>
            }
        </>
    )
}
