"use client"

import { useEffect, useState } from "react"
import { useChainId, useAccount, useReadContract } from "wagmi"
import { zeroAddress } from "viem"
import toast from "react-hot-toast"
import { ethers } from "ethers"
import { Copy, Users, Coins, Share2, Gift } from "lucide-react"

import { BASE_NET, MINER_ADDRESS_BASE, MINER_ADDRESS_MAIN } from "../config"
import MinerAbi from "../config/abis/MINER_ABI.json"

export default function Referral() {
  const chainId = useChainId()
  const { isConnected, address } = useAccount()

  const [referralLink, setReferralLink] = useState("")
  const [copied, setCopied] = useState(false)

  const { data: referredUsers, refetch: refetchReferredUsers } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "getReferrals",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  })

  const { data: referralBonusPaid, refetch: refetchReferralBonusPaid } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "referralBonusPaid",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  })

  useEffect(() => {
    if (!isConnected || !address) return

    setReferralLink(`${window.location.origin}?ref=${address}`)
  }, [address])

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(referralLink)
      toast.success("Referral link copied!")
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    } catch (err) {
      console.error("Failed to copy:", err)
      toast.error("Failed to copy link")
    }
  }

  const formatEarnings = (value: any) => {
    if (!value) return "0.00"
    try {
      return Number.parseFloat(ethers.formatEther(value)).toFixed(2)
    } catch {
      return "0.00"
    }
  }

  return (
    <div className="p-4 text-white bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900">
      <div className="max-w-md mx-auto">
        {/* Header */}
        <div className="text-center mb-4">
          <div className="inline-flex items-center justify-center w-12 h-12 bg-gradient-to-r from-yellow-500 to-orange-400 rounded-full mb-2">
            <Gift className="w-6 h-6 text-slate-900" />
          </div>
          <h2 className="text-xl font-bold mb-1 bg-gradient-to-r from-yellow-400 to-orange-400 bg-clip-text text-transparent">
            REFER A FRIEND
          </h2>
          <p className="text-slate-300 text-xs leading-relaxed">
            Share your link and earn <span className="text-yellow-400 font-bold">5.0%</span> bonus!
          </p>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-2 gap-3 mb-4">
          <div className="bg-[#122533] rounded-lg p-4 border border-slate-700/50 hover:border-yellow-400/30 transition-all duration-300">
            <div className="flex items-center justify-between mb-2">
              <Users className="w-4 h-4 text-yellow-400" />
              <div className="w-1.5 h-1.5 bg-green-400 rounded-full animate-pulse"></div>
            </div>
            <p className="text-slate-400 text-xs uppercase tracking-wide mb-1">Referrals</p>
            <p className="text-xl font-bold text-yellow-400">{referredUsers ? referredUsers.length : 0}</p>
          </div>

          <div className="bg-[#122533] rounded-lg p-4 border border-slate-700/50 hover:border-yellow-400/30 transition-all duration-300">
            <div className="flex items-center justify-between mb-2">
              <Coins className="w-4 h-4 text-yellow-400" />
              <div className="w-1.5 h-1.5 bg-yellow-400 rounded-full animate-pulse"></div>
            </div>
            <p className="text-slate-400 text-xs uppercase tracking-wide mb-1">Earned</p>
            <p className="text-xl font-bold text-yellow-400">
              {formatEarnings(referralBonusPaid)} <span className="text-xs text-slate-400">$HASH</span>
            </p>
          </div>
        </div>

        {/* Connection Status */}
        {!isConnected && (
          <div className="bg-red-500/10 border border-red-500/20 rounded-lg p-3 mb-4">
            <p className="text-red-400 text-xs font-semibold text-center">Connect wallet to view referral data</p>
          </div>
        )}

        {/* Referral Link */}
        <div className="bg-[#122533] rounded-lg p-4 border border-slate-700/50 mb-4">
          <div className="flex items-center gap-2 mb-3">
            <Share2 className="w-4 h-4 text-yellow-400" />
            <h3 className="text-xs font-semibold text-slate-200 uppercase tracking-wide">Referral Link</h3>
          </div>

          <div className="bg-slate-800/50 rounded p-3 border border-slate-600/30">
            <div className="flex items-center justify-between gap-2">
              <span className="text-xs text-slate-300 break-all font-mono bg-slate-900/50 px-2 py-1 rounded flex-1">
                {referralLink || "Connect wallet to generate link"}
              </span>
              <button
                onClick={handleCopy}
                disabled={!referralLink}
                className={`flex items-center gap-1 px-3 py-1 rounded font-semibold text-xs transition-all duration-200 ${
                  copied
                    ? "bg-green-500 text-white"
                    : referralLink
                      ? "bg-gradient-to-r from-yellow-500 to-orange-400 text-slate-900 hover:from-yellow-400 hover:to-orange-300"
                      : "bg-slate-600 text-slate-400 cursor-not-allowed"
                }`}
              >
                <Copy className="w-3 h-3" />
                {copied ? "COPIED!" : "COPY"}
              </button>
            </div>
          </div>
        </div>

        {/* Bonus Info */}
        <div className="bg-gradient-to-r from-yellow-500/10 to-orange-400/10 border border-yellow-400/20 rounded-lg p-3 mb-4">
          <div className="text-center">
            <div className="inline-flex items-center gap-1 text-yellow-400 mb-1">
              <Gift className="w-3 h-3" />
              <span className="text-xs font-semibold">5% BONUS STRUCTURE</span>
            </div>
            <p className="text-slate-300 text-xs">Earn from all $HASH mined by referrals</p>
          </div>
        </div>

        {/* Network Info - Compact */}
        {/* {isConnected && (
          <div className="bg-slate-800/30 rounded-lg p-3 mb-4 border border-slate-700/30">
            <div className="flex items-center justify-between text-xs">
              <span className="text-slate-400">
                {chainId === BASE_NET ? "Testnet" : "Mainnet"}  {`${address?.slice(0, 6)}...${address?.slice(-4)}`}
              </span>
            </div>
          </div>
        )} */}

        {/* Close Button */}
        {/* <button className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:from-yellow-400 hover:to-orange-300 text-slate-900 font-bold py-3 px-6 rounded-lg text-sm transition-all duration-300 hover:scale-105">
          CLOSE
        </button> */}
      </div>
    </div>
  )
}
