"use client"

import { useEffect, useState } from "react"
import { useChainId, useAccount, useReadContract, useWriteContract, useBalance } from "wagmi"
import { zeroAddress } from "viem"
import { ethers } from "ethers"

import { BASE_NET, MINER_ADDRESS_BASE, MINER_ADDRESS_MAIN } from "../config"
import MinerAbi from "../config/abis/MINER_ABI.json"

import ImgLeaf from "../assets/images/miners/leaf.png"
import ImgSteel from "../assets/images/miners/steel.png"
import ImgDiamond from "../assets/images/miners/diamond.png"
import toast from "react-hot-toast"

const ary = Array.from({ length: 10000 }, (_, i) => ({
  isCancelling: false,
  isBuying: false,
}))

export default function Marketplace() {
  const chainId = useChainId()
  const { isConnected, address } = useAccount()
  const { writeContractAsync } = useWriteContract()

  const { data: balance, refetch: refetchBalance } = useBalance({ address: address });

  const { data: playerMinersOwned, refetch: refetchPlayerMinersOwned } = useReadContract({
    address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "getMinersOnMarketplace",
    args: [],
    chainId: chainId,
  })

  const [selfMiners, setSelfMiners] = useState([])
  const [otherMiners, setOtherMiners] = useState([])
  const [btnStatus, setBtnStatus] = useState(ary)

  useEffect(() => {
    if (
      address === undefined ||
      address === zeroAddress ||
      !isConnected ||
      playerMinersOwned === undefined ||
      chainId === undefined
    )
      return

    const selfMinersTemp = []
    const otherMinersTemp = []
    playerMinersOwned.forEach((item) => {
      if (item.owner.toLowerCase() === address.toLowerCase()) {
        selfMinersTemp.push(item)
      } else {
        otherMinersTemp.push(item)
      }
    })

    setSelfMiners(selfMinersTemp)
    setOtherMiners(otherMinersTemp)
  }, [address, playerMinersOwned, isConnected, chainId])

  const handleCancelListing = (id, index) => async () => {
    if (address === undefined || address === zeroAddress || !isConnected || chainId === undefined) {
      return
    }

    const temp = [...btnStatus]
    temp[index].isCancelling = true
    setBtnStatus(temp)

    try {
      await writeContractAsync({
        address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "cancelListForSale",
        args: [id],
      })
      await refetchPlayerMinersOwned()
    } catch (err) {
      console.error("Cancel listing error:", err)
    } finally {
      temp[index].isCancelling = false
      setBtnStatus(temp)
    }
  }

  const handleBuying = (id, price, index) => async () => {
    if (address === undefined || address === zeroAddress || !isConnected || chainId === undefined) {
      return
    }

    if (balance.value < price) {
      toast.error("Insufficient Balance!");
      return;
    }

    const temp = [...btnStatus]
    temp[index].isBuying = true
    setBtnStatus(temp)

    try {
      await writeContractAsync({
        address: chainId === BASE_NET ? MINER_ADDRESS_BASE : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "buyMinerFromMarket",
        args: [id],
        value: price,
      })
      await refetchPlayerMinersOwned()
    } catch (err) {
      console.error("Buy miner error:", err)
    } finally {
      temp[index].isBuying = false
      setBtnStatus(temp)
    }
  }

  const getMinerImage = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return ImgLeaf
      case 2:
        return ImgSteel
      case 3:
        return ImgDiamond
      default:
        return ImgLeaf
    }
  }

  const getMinerType = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return "Leaf"
      case 2:
        return "Steel"
      case 3:
        return "Diamond"
      default:
        return "Unknown"
    }
  }

  const getMinerTypeColor = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return "text-green-400"
      case 2:
        return "text-gray-400"
      case 3:
        return "text-blue-400"
      default:
        return "text-gray-400"
    }
  }

  const getMinerTypeBg = (minerIndex) => {
    switch (Number(minerIndex)) {
      case 1:
        return "from-green-500/20 to-emerald-400/20"
      case 2:
        return "from-gray-500/20 to-slate-400/20"
      case 3:
        return "from-blue-500/20 to-cyan-400/20"
      default:
        return "from-gray-500/20 to-slate-400/20"
    }
  }

  const EmptyState = ({ message, icon }) => (
    <div className="flex flex-col items-center justify-center py-16 px-4">
      <div className="text-6xl mb-4 opacity-50">{icon}</div>
      <p className="text-gray-400 text-lg font-medium text-center">{message}</p>
    </div>
  )

  const MinerCard = ({ miner, index, isOwned = false }) => {
    const minerType = getMinerType(miner.minerIndex)
    const minerImage = getMinerImage(miner.minerIndex)
    const typeColor = getMinerTypeColor(miner.minerIndex)
    const typeBg = getMinerTypeBg(miner.minerIndex)

    return (
      <div className="group relative bg-[#0000009e] backdrop-blur-sm rounded-xl p-6 border border-[#F8A82C]/30 hover:border-[#F8A82C] transition-all duration-300 hover:shadow-lg hover:shadow-[#F8A82C]/10">
        <div
          className={`absolute inset-0 bg-gradient-to-r ${typeBg} rounded-xl blur-sm opacity-0 group-hover:opacity-100 transition-opacity duration-300`}
        ></div>

        <div className="relative flex items-center justify-between">
          <div className="lg:flex items-center space-x-6">
            <div className="relative">
              <div className={`absolute inset-0 bg-gradient-to-r ${typeBg} rounded-full blur-md`}></div>
              <img
                src={minerImage || "/placeholder.svg"}
                alt={`${minerType} Miner`}
                className="relative h-16 w-16 rounded-full border-2 border-gray-600 group-hover:border-yellow-500/50 transition-colors duration-300 object-cover"
              />
              <div className="absolute -top-1 -right-1 bg-gradient-to-r from-[#F9A82C] to-[#FFD100] text-black text-xs font-bold px-2 py-1 rounded-full">
                Lv.{Number(miner.level)}
              </div>
            </div>

            <div className="space-y-2">
              <div className="flex items-center space-x-3">
                <span className={`font-bold text-xl ${typeColor}`}>{minerType}</span>
                <span className="text-gray-400 text-sm bg-gray-700/50 px-2 py-1 rounded-full">Miner</span>
              </div>
              <div className="flex items-center space-x-6 text-sm">
                <div className="flex items-center space-x-2">
                  <svg className="w-4 h-4 text-yellow-400" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2M12,4A8,8 0 0,1 20,12A8,8 0 0,1 12,20A8,8 0 0,1 4,12A8,8 0 0,1 12,4M12,6A6,6 0 0,0 6,12A6,6 0 0,0 12,18A6,6 0 0,0 18,12A6,6 0 0,0 12,6Z" />
                  </svg>
                  <span className="text-gray-300">Level {Number(miner.level)}</span>
                </div>
                <div className="flex items-center space-x-2">
                  <svg className="w-4 h-4 text-green-400" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M7,15H9C9,16.08 10.37,17 12,17C13.63,17 15,16.08 15,15C15,13.9 13.96,13.5 11.76,12.97C9.64,12.44 7,11.78 7,9C7,7.21 8.47,5.69 10.5,5.18V3H13.5V5.18C15.53,5.69 17,7.21 17,9H15C15,7.92 13.63,7 12,7C10.37,7 9,7.92 9,9C9,10.1 10.04,10.5 12.24,11.03C14.36,11.56 17,12.22 17,15C17,16.79 15.53,18.31 13.5,18.82V21H10.5V18.82C8.47,18.31 7,16.79 7,15Z" />
                  </svg>
                  <span className="text-[#FFD100] font-semibold">{parseFloat(ethers.formatEther(miner.sellPrice)).toFixed(4)} ETH</span>
                </div>
              </div>
            </div>

            <div className="flex items-center">
              {isOwned ? (
                <button
                  className="bg-gradient-to-r from-red-600 to-red-700 hover:from-red-500 hover:to-red-600 disabled:from-gray-600 disabled:to-gray-700 text-white font-semibold px-6 py-3 rounded-lg transition-all duration-300 transform hover:scale-105 active:scale-95 disabled:hover:scale-100 disabled:cursor-not-allowed min-w-[140px]"
                  onClick={handleCancelListing(miner.id, index)}
                  disabled={btnStatus[index]?.isCancelling}
                >
                  {btnStatus[index]?.isCancelling ? (
                    <div className="flex items-center justify-center space-x-2">
                      <svg className="animate-spin h-4 w-4" fill="none" viewBox="0 0 24 24">
                        <circle
                          className="opacity-25"
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                        ></circle>
                        <path
                          className="opacity-75"
                          fill="currentColor"
                          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        ></path>
                      </svg>
                      <span>Cancelling...</span>
                    </div>
                  ) : (
                    "Cancel Listing"
                  )}
                </button>
              ) : (
                <button
                  className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-500 hover:to-green-600 disabled:from-gray-600 disabled:to-gray-700 text-white font-semibold px-6 lg:py-3 py-2 w-[100%] lg:mt-[0px] mt-[25px] rounded-lg transition-all duration-300 transform hover:scale-105 active:scale-95 disabled:hover:scale-100 disabled:cursor-not-allowed min-w-[120px]"
                  onClick={handleBuying(miner.id, miner.sellPrice, index)}
                  disabled={btnStatus[index]?.isBuying}
                >
                  {btnStatus[index]?.isBuying ? (
                    <div className="flex items-center justify-center space-x-2">
                      <svg className="animate-spin h-4 w-4" fill="none" viewBox="0 0 24 24">
                        <circle
                          className="opacity-25"
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                        ></circle>
                        <path
                          className="opacity-75"
                          fill="currentColor"
                          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        ></path>
                      </svg>
                      <span>Buying...</span>
                    </div>
                  ) : (
                    "Buy Now"
                  )}
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-black p-6">
      <div className="max-w-7xl mx-auto space-y-8">
        {/* Header */}
        <div className="text-center space-y-4">
          <h1 className="text-transparent bg-gradient-to-r from-[#F9A82C] to-[#FFD100] bg-clip-text font-bold text-4xl lg:text-5xl">
            Miner Marketplace
          </h1>
          <p className="text-gray-400 text-lg max-w-2xl mx-auto">
            Trade your miners with other players. List your miners for sale or browse available miners to purchase.
          </p>
        </div>

        {/* Your Listed Miners Section */}
        <div className="space-y-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="text-4xl"></div>
              <div>
                <h2 className="text-[#F9A82C] font-bold text-2xl lg:text-3xl">Your Listed Miners</h2>
                <p className="text-gray-400 text-sm">Miners you have listed for sale</p>
              </div>
            </div>
            {selfMiners.length > 0 && (
              <div className="bg-[#F8A82C]/20 text-[#F8A82C] px-4 py-2 rounded-full text-sm font-semibold border border-[#F8A82C]/30">
                {selfMiners.length} Listed
              </div>
            )}
          </div>

          <div className="bg-[#0000009e] overflow-y-auto h-64 backdrop-blur-xl rounded-2xl border border-[#F8A82C]/20 overflow-hidden">
            {selfMiners.length > 0 ? (
              <div className="divide-y divide-gray-700/50">
                {selfMiners.map((miner, index) => (
                  <div key={miner.id} className="p-6">
                    <MinerCard miner={miner} index={index} isOwned={true} />
                  </div>
                ))}
              </div>
            ) : (
              <EmptyState
                message="You haven't listed any miners for sale yet. Start mining and list your miners here!"
                icon=""
              />
            )}
          </div>
        </div>

        {/* Available Miners Section */}
        <div className="space-y-6">
          <div className="lg:flex block items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="text-4xl"></div>
              <div>
                <h2 className="text-[#F9A82C] font-bold text-2xl lg:text-3xl">Available Miners</h2>
                <p className="text-gray-400 text-sm">Miners available for purchase from other players</p>
              </div>
            </div>
            {otherMiners.length > 0 && (
              <div className="bg-[#F8A82C]/20 text-[#F8A82C] px-4 py-2 rounded-full text-sm font-semibold border border-[#F8A82C]/30">
                {otherMiners.length} Available
              </div>
            )}
          </div>

          <div className="bg-[#0000009e] overflow-y-auto h-96 backdrop-blur-xl rounded-2xl border border-[#F8A82C]/20 overflow-hidden">
            {otherMiners.length > 0 ? (
              <div className="divide-y divide-gray-700/50">
                {otherMiners.map((miner, index) => (
                  <div key={miner.id} className="p-6">
                    <MinerCard miner={miner} index={index} isOwned={false} />
                  </div>
                ))}
              </div>
            ) : (
              <EmptyState
                message="No miners available for purchase at the moment. Check back later for new listings!"
                icon=""
              />
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
